	border-bottom: 1px solid #dedede;
  }
  
  th,
  td {
	text-align: left;
	padding: 0.5rem;
  }
  
  caption {
	padding: 1rem 0;
	caption-side: bottom;
	color: #ababab;
  }
  
  .striped-table tbody tr:nth-child(odd) {
	background-color: #f8f8f8;
  }
  
  .contain-table {
	overflow-x: auto;
  }
  
  @media (min-width: 600px) {
	.contain-table {
	  width: 100%;
	}
  }
  
